@import "~antd/dist/antd.css";

* {
  padding: 0;
  margin: 0;
}

.root {
  background-color: pink;
}

a {
  color: #ccc;
}

li {
  list-style: none;
}

.sprite_02 {
  background: url(../img/sprite_02.png);
}

.show {
  display: block;
}

.hidden {
  display: none;
}

.wrong {
  background-color: red;
}

.right {
  background-color: rgb(52, 221, 52);
}

.right-selection {
  border-color: rgb(15, 214, 15) !important;
}
